/* Colors */
main {
  font-size: 130%;
  font-weight: bolder;
  color: #000000;
  margin: 1px 30px;
  border-radius: 2rem;
  text-align: justify;
}

.cta {
  background-color: #fe142f;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  text-transform: capitalize;
  font-size: 14px;
  height: 31px;
  padding-left: 31.5px;
  padding-right: 31.5px;
  margin: 0;
  margin-top: 59px;
  border-radius: 8px;
  border: 0;
  cursor: pointer;
}
.cta span {
  display: inline-block;
  white-space: nowrap;
}

.button {
  background: #fe142f;
  margin-top: 0.5em;
  padding: 1em;
  color: #ffffff;
  border-radius: 15px;
  cursor: pointer;
  font-size: 16px;
}
.button:hover {
  background: #3876ac;
}

.smFont {
  font-size: 16px;
}

@media (width > 540px) {
  main {
    margin: 1px 60px;
  }
  .cta {
    font-size: 25px;
    height: 55px;
  }
}